@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
:root {
    --top-menu-height: 4em;
}

/* @import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,200;0,400;0,800;1,200;1,400;1,800&display=swap'); */


/**
 * The main scss
 */
body {
  font-family: 'Rubik', arial, sans-serif;
  font-size: 14px;
  line-height: 1.4;
  font-weight: 400;
  background-color: #e5e9ed; }
  body .ui.menu {
    font-family: 'Rubik', arial, sans-serif;
    font-size: 14px;
    line-height: 1.4; }
  body h1, body h2, body h3, body h4, body h5 {
    font-family: 'Rubik', arial, sans-serif; }

.m__site .side {
  transition: width 0.3s;
  width: 14em;
  overflow: hidden;
  padding-top: var(--top-menu-height);
  z-index: 99; }
  .m__site .side.small-side.ui.vertical.menu {
    width: 4em;
    flex-basis: 4em; }
    .m__site .side.small-side.ui.vertical.menu .menu-item__txt {
      display: none; }

.m__site .ui.vertical.menu {
  background-color: #3c4b64;
  width: 14rem; }

.parent {
  display: flex; }

.mcontent {
  position: absolute;
  right: 0;
  transition: all 0.3s;
  width: calc(100% - 14em); }
  .mcontent.small-content {
    width: calc(100% - 4em); }

.main-content {
  grid-area: maincontent; }
  .main-content .page-content-wp .page-content__ct {
    padding: 20px; }

.m__site .ui.segment {
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
  border: none; }

.m__site .wt-box {
  background-color: #fff;
  box-shadow: 0 1px 4px 0 rgba(34, 36, 38, 0.15); }

.wt-box .breadcrumb {
  background-color: transparent;
  padding: 15px 20px; }

.m__site .ui.breadcrumb a {
  color: #3c4b64; }
  .m__site .ui.breadcrumb a:hover {
    color: #303c54; }

.page-content__ct .ui.grid {
  margin: 0;
  display: block; }
  .page-content__ct .ui.grid > .row {
    padding-top: 0;
    padding-bottom: 0;
    margin-left: -1rem;
    margin-right: -1rem;
    display: flex;
    width: auto !important; }
    .page-content__ct .ui.grid > .row > .column {
      display: flex;
      justify-content: center; }

.grid.m__site {
  width: 100%;
  display: grid;
  grid: var(--top-menu-height)/1fr;
  grid-template-areas: "menu" "maincontent";
  min-height: 100vh; }

/* SPACING */
.ml-5 {
  margin-left: 5px; }

.mr-10 {
  margin-right: 10px; }

.mr-15 {
  margin-right: 15px; }

.mr-20 {
  margin-right: 15px; }

.mb-30 {
  margin-bottom: 30px; }

.navbar li .glyphicon {
  margin-right: 10px; }

/* Highlighting rules for nav menu items */
.navbar .navbar-nav .active a,
.navbar .navbar-nav .active a:hover,
.navbar .navbar-nav .active a:focus {
  background-image: none;
  background-color: #4189C7;
  color: white; }

.logo-space {
  margin: 0; }

.top-menu {
  height: var(--top-menu-height); }

.menu {
  grid-area: menu;
  z-index: 100; }

.logo-space-menu-item {
  width: 14em; }

.display-inline {
  display: inline-flex;
  align-items: center; }

.logo-space {
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  margin: 0; }

.logo-space img, .display-inline img {
  margin-right: 8px; }

.no-border::before {
  display: none; }

.top-menu i.icon {
  margin: 0 !important; }

.drop-left-padding {
  padding-left: 0 !important; }

.label-on-corner {
  top: 0.8em !important;
  left: 78% !important; }

@media (min-width: 768px) {
  /* On large screens, convert the nav menu to a vertical sidebar */
  .navbar {
    height: 100%;
    width: calc(25% - 20px); }
  .navbar {
    border-radius: 0;
    border-width: 0;
    height: 100%; }
  .navbar-header {
    float: none; }
  .navbar .navbar-collapse {
    border-top: 1px solid #444;
    padding: 0; }
  .navbar .container-fluid {
    padding: 0;
    margin: 0; }
  .navbar .container-fluid .navbar-brand {
    margin: 0; }
  .navbar ul {
    float: none; }
  .navbar li {
    float: none;
    font-size: 15px;
    margin: 6px; }
  .navbar li a {
    padding: 10px 16px;
    border-radius: 4px; }
  .navbar a {
    /* If a menu item's text is too long, truncate it */
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; } }

/**
 * HEADER CSS
 */
.page-header__top.menu > .ui.top {
  background: #303c54;
  box-shadow: 0 1px 1px 0px #00000050; }

.logo__txt {
  font-size: 1.2rem;
  margin-left: 10px;
  color: #ffffff; }

.side-menu__left .ui.menu a.item:hover, .page-header__top .ui.menu a.item:hover {
  background: none;
  color: #ffffff; }

.ui.menu .call-sidebar-menu {
  font-size: 16px;
  padding-left: 4px;
  padding-right: 4px;
  color: #ffffff; }

.c-icon {
  display: inline-block;
  color: #dddddd;
  text-align: center; }

.c-icon:not(.c-icon-c-s):not(.c-icon-custom-size) {
  width: 20px;
  height: 20px;
  font-size: 1rem; }

/* Label Floating Number */
.page-header__top .ui.menu .item.logo-space-menu-item:before {
  display: none; }

.page-header__top .ui.mini.label {
  font-size: 10px;
  font-weight: 400;
  padding: 2px  5px !important;
  height: 18px;
  line-height: 18px; }

a.menu__item {
  display: block; }
  a.menu__item:hover {
    color: #ffffff; }

.profile__link {
  font-size: 0.9rem;
  color: #eeeeee; }
  .profile__link:active, .profile__link:visited, .profile__link:focus {
    color: #eeeeee; }
  .profile__link:hover {
    text-decoration: underline;
    color: #eeeeee; }

/* LEFT SIDE */
.ico {
  display: inline-block;
  width: 16px;
  height: 16px;
  font-size: 1rem;
  color: #ffffff; }
  .ico.ico-medium {
    width: 20px;
    height: 20px; }
  .ico.grey {
    color: #999999; }

.side-menu__left.ui.vertical.menu a.item {
  position: relative; }
  .side-menu__left.ui.vertical.menu a.item .ico {
    transition: color 0.2s ease-out; }
  .side-menu__left.ui.vertical.menu a.item .menu-item__txt {
    color: #ddd;
    transition: all 0.1s ease-out; }
  .side-menu__left.ui.vertical.menu a.item .label {
    position: absolute;
    top: 14px;
    right: 10px; }
  .side-menu__left.ui.vertical.menu a.item:hover {
    background-color: rgba(0, 0, 0, 0.1); }
    .side-menu__left.ui.vertical.menu a.item:hover .ico {
      color: #fff; }
    .side-menu__left.ui.vertical.menu a.item:hover .menu-item__txt {
      color: #fff; }

.side-menu__left.ui.vertical.menu .active.item,
.side-menu__left.ui.vertical.menu .active.item:hover,
.side-menu__left.ui.vertical.menu .active.item:hover {
  background-color: rgba(0, 0, 0, 0.2); }
  .side-menu__left.ui.vertical.menu .active.item .ico,
  .side-menu__left.ui.vertical.menu .active.item:hover .ico,
  .side-menu__left.ui.vertical.menu .active.item:hover .ico {
    color: #fff; }
  .side-menu__left.ui.vertical.menu .active.item .menu-item__txt,
  .side-menu__left.ui.vertical.menu .active.item:hover .menu-item__txt,
  .side-menu__left.ui.vertical.menu .active.item:hover .menu-item__txt {
    color: #fff; }

/** 
 * HOME PAGE
 */
.card.num-card {
  color: #fff;
  background: #1f1498;
  background: linear-gradient(45deg, #321fdb, #1f1498);
  border-color: #1f1498;
  position: relative;
  min-height: 150px; }
  .card.num-card.num-card__dblue {
    background: #1f1498;
    background: linear-gradient(45deg, #321fdb, #1f1498);
    border-color: #1f1498; }
  .card.num-card.num-card__lblue {
    background: #2982cc;
    background: linear-gradient(45deg, #39f, #2982cc);
    border-color: #2982cc; }
  .card.num-card.num-card__yellow {
    background: #f6960b;
    background: linear-gradient(45deg, #f9b115, #f6960b);
    border-color: #f6960b; }
  .card.num-card.num-card__orange {
    background: #d93737;
    background: linear-gradient(45deg, #e55353, #d93737);
    border-color: #d93737; }
  .card.num-card .content {
    color: #fff; }
    .card.num-card .content > .header {
      color: #fff; }
      .card.num-card .content > .header > span.num-card__sign {
        font-size: 14px;
        vertical-align: top; }
      .card.num-card .content > .header:not(.ui) {
        font-size: 1.8rem;
        padding-top: 10px;
        font-weight: 700; }
      .card.num-card .content > .header + .description {
        color: rgba(255, 255, 255, 0.7);
        padding-right: 60px; }
  .card.num-card .num-card__img {
    position: absolute;
    bottom: 10px;
    right: 10px;
    width: 60px;
    opacity: 0.3; }

/**
 * RESPONSIVE CSS
 */
.ios-bottom-bar {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 100;
  background-color: #303c54;
  height: 45px;
  box-shadow: 0 -1px 2px 0px #00000050; }
  .ios-bottom-bar .ui.menu {
    background-color: transparent;
    border: none; }
    .ios-bottom-bar .ui.menu .item {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
      align-items: center;
      justify-content: center;
      transition: background-color 0.25s  ease-out; }
      .ios-bottom-bar .ui.menu .item.active {
        color: #ffffff;
        background-color: rgba(0, 0, 0, 0.2); }
        .ios-bottom-bar .ui.menu .item.active .ico {
          color: #ffffff; }

/* Tablet Screen - 979px */
@media (max-width: 979px) {
  .m__site .parent > .side {
    position: fixed; }
  .m__site .ui.vertical.menu {
    width: 0;
    flex-basis: 0; }
    .m__site .ui.vertical.menu.side.open-mb-menu {
      width: 14rem;
      flex-basis: 14rem; }
      .m__site .ui.vertical.menu.side.open-mb-menu.small-side {
        width: 14rem;
        flex-basis: 14rem; }
    .m__site .ui.vertical.menu.side.small-side {
      width: 0;
      flex-basis: 0; }
      .m__site .ui.vertical.menu.side.small-side .menu-item__txt {
        display: inline-block; }
  .m__site .mcontent {
    width: 100%; }
    .m__site .mcontent.small-content {
      width: 100%; }
  .ui.grid > .stackable.stackable.row > .column,
  .ui.stackable.grid > .column.grid > .column,
  .ui.stackable.grid > .column.row > .column,
  .ui.stackable.grid > .column:not(.row),
  .ui.stackable.grid > .row > .column,
  .ui.stackable.grid > .row > .wide.column,
  .ui.stackable.grid > .wide.column {
    padding-left: 0;
    padding-right: 0;
    padding-top: 10px;
    padding-bottom: 10px; }
  .mb-30 {
    margin-bottom: 0px; }
  .main-content .page-content-wp .page-content__ct {
    padding-bottom: 60px; } }

@media (max-width: 480px) {
  .logo-space-menu-item {
    width: auto; } }

